import {
	setClipboardData,
	getClipboardData
} from '@/uni_modules/u-clipboard/js_sdk'
import ssPaymentPassword from '../../components/sanshui-payment-password'

export default {
	components: {
		ssPaymentPassword
	},
	data() {
		return {
			acer_truncatum: 0.00, //金元宝
			gold_balance: 0.00, //银元宝
			balance: 0.00, //用户余额
		};
	},
	onShow: function(e) {
		this.$store.commit('judgeLogin'); //判断登录状态
		this.getUserData();
	},
	onLoad(option) {},
	methods: {
		dump(url) {
			uni.navigateTo({
				url: url
			});
		},
		getUserData() {
			//获取用户信息
			var _this = this;
			_this.$request.user
				.getUserData({})
				.then(data => {
					if (data.status == 1) {
						_this.balance = data.data.balance;
						_this.acer_truncatum = data.data.acer_truncatum;
						_this.gold_balance = data.data.gold_balance;
					} else {
						uni.showToast({
							icon: 'none',
							title: data.msg
						});
					}
				})
				.catch(err => {
					//消息异常
					uni.showToast({
						icon: 'none',
						title: '数据加载异常'
					});
				});
		}
	}
};
